
// #region menu

.menu {
  position: fixed;
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  align-items: center;
  font-weight: 700;
  text-transform: uppercase;

  .menu__content {
    padding-inline: 20px;
  }

  @media (min-width: 576px) {
    .menu__content {
      padding-inline: 72px;
    }
  }

  .menu__nav {
    margin-bottom: 40px;
    margin-top: 24px;
  }

  .menu__contacts{
    margin-left: 20px;

    .menu__phone__number {
      display: block;
      color: inherit;
      text-decoration: none;
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }

    .menu__book__ride {
      display: block;
      color: inherit;
      padding-bottom: 7px;
      border-bottom: 1px solid #1D1D1D;
      width: fit-content;
      text-decoration: none;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
    }
  }

  @media (min-width:576px) {
    .menu__contacts {
      margin-left: 72px;
    }
  }
}


@media (min-width: 576px) {
  .menu__nav {
    margin-bottom: 40px;
    margin-top: 8px;
  }
}

// #endregion

// #region nav

.nav__list {
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin: 0;
  padding: 0;
  list-style: none;

  .nav__link {
    color: inherit;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
  }
}

// #end region
